body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

.nav-item .active {
  border-bottom: 2px solid #61b8b5;
  font-weight: bold;
  color: #90d4d1 !important;
}

.nav-item:hover, button:hover, .button:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.cards {
  width: 300px;
  height: 75vh;
  transition: -webkit-transform .3s ease-in-out, transform .3s ease-in-out;
}

.cards:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.hero {
  background: linear-gradient(#cce9e9, #628d8d);
}

.temp {
  background: linear-gradient(#628d8d, #bbdbda);
}

.feat {
  background: linear-gradient(#bbdbda, #628d8d);
}

